.about_cont{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content:center;
    align-items: center;
    gap:150px;
}

.about__contact a{
  background-color: blue;
  color: whitesmoke;
}
.about_details{
    font-size: 45px;
}
.typo_div{
    display: flex;
    flex-direction: column;
    justify-content: center;align-items: center;
    gap: 50px;
}
.about_cont img{
    width: 90%;
    border-radius: 55%;
}


   .mobileHead{
    font-size: 45px;
    margin-bottom: -15px;
   }
  .about__name {
    color: var(--clr-primary);
  }
  
  .about__desc {
    font-size: 1rem;
    max-width: 70%;
    text-align: center;
  }
  
  .about__desc,
  .about__contact {
    margin-top: 2.4em;
  }
  
  .about .link--icon {
    margin-right: 0.8em;
  }
  
  .about .btn--outline {
    margin-right: 1em;
  }
  .btnResume{
    color: var(--clr-primary);
    border: 2px solid var(--clr-primary);
    border-radius: 5px;
    height: 50px;
    cursor: pointer;
    padding: 0px 30px;
    margin-top: 10px;
  }
  .btnResume:hover{
    background-color: var(--clr-primary);
    color: var(--clr-bg);
  }
  
   
